<template>
  <div class="step_two_summary">
    <div class="container">
      <div>
        <h2>Wybrane strony:</h2><br>
        <router-link to="/home/step_one">Edytuj</router-link>
        <p v-if="$store.state.Front.boolean">Przód<br><button id="frontButton" @click="buttonConf('frontButton')">Potwierdzam</button>
        </p>
        <p v-if="$store.state.Back.boolean">Tył<br><button id="backButton" @click="buttonConf('backButton')">Potwierdzam</button>
        </p>
      </div>
      <div class="picsum">
        <h2>Wybrane picsum:</h2><br>
        <router-link to="/home/step_two">Edytuj</router-link>
        <div class="picsum_container">
        <p v-if="$store.state.Front.boolean">Dla przodu:
        <br>
        <img :src="$store.state.Front.url"><br><button id="frontImgButton" @click="buttonConf('frontImgButton')">Potwierdzam</button>
        </p>
        <p v-if="$store.state.Back.boolean">Dla tyłu:
        <br>
        <img :src="$store.state.Back.url"><br><button id="backImgButton" @click="buttonConf('backImgButton')">Potwierdzam</button>
        </p>
      </div></div>
      <div class="effects">
        <h2>Wybrane efekty:</h2><br>
        <router-link to="/home/step_two_addition">Edytuj</router-link>
        <p v-if="$store.state.Front.boolean">Dla przodu:</p>
        <div class="effect_container"><p>
        <span v-if="$store.state.Front.gray.boolean">Grayscale {{ $store.state.Front.gray.scale }}<button id="frontGrayButton" @click="buttonConf('frontGrayButton')">Potwierdzam</button></span>
        <span v-if="$store.state.Front.blur.boolean">Blur {{ $store.state.Front.blur.scale }}<button id="frontBlurButton" @click="buttonConf('frontBlurButton')">Potwierdzam</button></span><br>
        </p></div>
        <p v-if="$store.state.Back.boolean">Dla tyłu:</p>
        <div class="effect_container"><p>
        <span v-if="$store.state.Back.gray.boolean">Grayscale {{ $store.state.Back.gray.scale }}<button id="backGrayButton" @click="buttonConf('backGrayButton')">Potwierdzam</button></span>
        <span v-if="$store.state.Back.blur.boolean">Blur {{ $store.state.Back.blur.scale }}<button id="backBlurButton" @click="buttonConf('backBlurButton')">Potwierdzam</button></span><br>
        </p></div>
      </div></div>
        <div class="nav">
        <router-link to="/home/step_two_addition">
      <button>← Wstecz</button>
    </router-link>
      <div  class="right_nav">
        <button v-if="!boolCheck()">Potwierdź<br> wybory</button>
        <router-link v-if="boolCheck()" to="/home/step_three"><button >Koszyk →</button></router-link>
      </div>
    </div>
  <div></div>
  </div>
</template>
<script>
export default {
  name: 'CartSummary',
  data: () => ({
    confirm: {
      front: true,
      back: true,
      frontImg: true,
      backImg: true,
      frontgray: true,
      frontblur: true,
      backgray: true,
      backblur: true
    }
  }),
  mounted () {
    if (this.$store.state.Front.boolean) {
      this.confirm.front = false
      this.confirm.frontImg = false
    }
    if (this.$store.state.Back.boolean) {
      this.confirm.back = false
      this.confirm.backImg = false
    }
    if (this.$store.state.Front.gray.boolean) {
      this.confirm.frontgray = false
    }
    if (this.$store.state.Front.blur.boolean) {
      this.confirm.frontblur = false
    }
    if (this.$store.state.Back.gray.boolean) {
      this.confirm.backgray = false
    }
    if (this.$store.state.Back.blur.boolean) {
      this.confirm.backblur = false
    }
  },
  methods: {
    boolCheck () {
      if (this.confirm.front && this.confirm.back && this.confirm.frontImg && this.confirm.backImg && this.confirm.frontgray && this.confirm.frontblur && this.confirm.backgray && this.confirm.backblur) {
        return true
      }
      return false
    },
    buttonConf (id) {
      document.getElementById(id).style.background = '#5de794'
      document.getElementById(id).innerText = 'Potwierdzono!'
      switch (id) {
        case 'frontButton':
          this.confirm.front = true
          break
        case 'backButton':
          this.confirm.back = true
          break
        case 'frontImgButton':
          this.confirm.frontImg = true
          break
        case 'backImgButton':
          this.confirm.backImg = true
          break
        case 'frontGrayButton':
          this.confirm.frontgray = true
          break
        case 'frontBlurButton':
          this.confirm.frontblur = true
          break
        case 'backGrayButton':
          this.confirm.backgray = true
          break
        case 'backBlurButton':
          this.confirm.backblur = true
          break
      }
    }
  }
}
</script>
<style lang="sass" scoped>
.step_two_addition
  flex-grow: 2
  z-index: 12
.effects
  display: grid
  p
    position: relative
    top: -40%
  .effect_container
    display: flex
    p
      display: flex
      gap: 1em
    span
      display: grid
.container
  display: flex
  justify-content: center
  gap: 5em
  max-height: 40vh
  a
    color: #5de794
    margin-bottom: 1rem
    transition: 0.5s
    &:hover
      color: #36475f
  button
    z-index: 13
    border: none
    border-radius: 25px
    padding: 10px
    font-size: 1rem
    font-weight: Bold
    transition: 1s
    cursor: pointer
    &:hover
      background-color: #5de794
      transform: scale(1.05)
  .picsum
    display: grid
    .picsum_container
      display: flex
      gap: 2em
  p
    font-size: 1.5em
  a
    font-size: 1.5em
  h2
    font-size: 2em
    margin-block-end: 0em
  img
    margin: 1em 0em 0em 0em
    width: 7em
    height: 7em
@media only screen and (max-width: 1366px)
  img
    width: 5em !important
    height: 5em !important
  .container
    p
      font-size: 1em
    a
      font-size: 1em
    h2
      font-size: 1.2em
    button
      padding: 5px
      font-size: 1em
  .preview
    .img
      width: 10em
      height: 10em

@media only screen and (max-width: 780px)
  div.nav
    height: 10em !important
  .container
    display: grid !important
    justify-content: center !important
    flex-direction: column !important
    align-items: center !important
    max-height: fit-content
</style>
